<template>
  <div>
    <el-form ref="form" :model="type" :rules="rules" label-width="100px">
      <el-form-item label="年 份" prop="year">
        <el-date-picker v-model="type.year" type="year" placeholder="选择年"  format="yyyy年" value-format="yyyy"></el-date-picker>
      </el-form-item>
      <el-form-item label="预算/收入" prop="type">
        <el-select v-model="type.type" placeholder="请选择文件类型">
          <el-option label="部门预算" value="budgets"></el-option>
          <el-option label="收入目标" value="incomings"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="上传文件">
          <el-upload  drag action="/ec/api/files/upload" 
          :limit="1" accept=".xlsx" :file-list="fileList" :auto-upload="false" ref="upload" :data="type" :with-credentials="true">
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
            <div class="el-upload__tip" slot="tip">*请先下载相应的预算模板，输入内容后再上传文件</div>
          </el-upload>
      </el-form-item>
      
      <el-form-item>
        <el-button type="success" @click="submitForm()" plain size="small">上 传</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  export default {
    name: 'upload',
    data() {
      return {
        type:{
          type: 'budgets',
          year: new Date(),
          userId: null
        },
        rules: {
          year: [
            { required: true, message: '请选择年份', trigger: 'change' }
          ],
          type: [
            { required: true, message: '请选择文件类型', trigger: 'change' }
          ],
        },
        fileList: []
      }
    },
    methods: {
      submitForm() {
        this.$refs.form.validate((valid) => {
          if (!valid) {
           return;
          }
          this.$refs.upload.submit();
        });
      }
    },
    created() {
      let user = JSON.parse(localStorage.getItem('user'))
      this.type.userId = user.userId;
    }
  }
</script>

<style scoped>
  .el-form-item__content {
      line-height: 20px !important;
      width:100%;
  }
  .el-upload-list {
    width: 360px;
  }
  .el-upload__tip {
    color: red;
  }
</style>